___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
}


body {
  font-family: 'Poppins', sans-serif;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}


/* 
skip to content
*/
.skip-link {
  position: absolute;
  top: -60px;
  left: 1.5rem;
  background-color: #362417;
  color: white;
  padding: 1.8rem;
  z-index: 100;
  font-weight: 400;
}

.skip-link:focus {
  top: 0;
}

/*
  AppBar
*/

.app-bar {
  padding: 8px 16px;
  background-color: white;
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  cursor: pointer;
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__brand h1 {
  font-family: 'Playfair Display', serif;
  color: #362417;
  font-size: 1.5rem;
  user-select: none;
}

.app-bar .app-bar__navigation {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px;
  background-color: white;
  overflow: hidden;
}

.app-bar .app-bar__navigation.open {
  left: 0;
}

.app-bar .app-bar__navigation ul li a { 
  display: inline-block;
  text-decoration: none;
  color: black;
  margin-bottom: 5px;
  width: 100%;
  font-size: 1.2rem;
  padding: 10px; 
  box-sizing: border-box; 
  min-width: 44px; 
  min-height: 44px; 
  display: inline-block; 
}

.app-bar .app-bar__navigation ul li a:hover {
  color: #744725;
}

.app-bar .app-bar__navigation ul li a::after {
  content: '';
  display: block;
  padding-bottom: 0.5rem;
  border-bottom: 0.5px solid #744725;
  transform: scaleX(0);
  transition: transform 0.3s linear;
}

.app-bar .app-bar__navigation ul li a:hover::after {
  transform: scaleX(0.2);
}


/*
  Main Content
*/

main {
  flex: 1;
}

.content {
  padding: 2rem;
  margin: 0 auto;
  min-height: 100%;
}

.content .content__heading {
  font-weight: normal;
  text-align: center;
}

/* 
Hero Section
*/

.hero-section {
  position: relative;
  text-align: center;
  background-color: #FFFBFF;
}

.hero-section img {
  width: 100%;
}


/*
  Restaurants
*/

.restaurants{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 2rem;
  margin-top: 32px;
}

/*
  Restaurant Item
*/

.restaurant-item {
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
  transform: scale(1.0);
  transition: transform 0.4s ease-in-out;
}

.restaurant-item:hover{
  transform: scale(1.03);
  box-shadow: 0 12px 16px rgba(0, 0, 0, 0.1);
}

.restaurant-item__header {
  position: relative;
}

.restaurant-item .restaurant-item__header .restaurant-item__header__poster {
  width: 100%;
  height: 250px;
  object-fit: cover; 
}

.restaurant-item .restaurant-item__header .restaurant-item__header__rating {
  position: absolute;
  padding: 8px;
  bottom: 20px;
  left: 0;
  display: inline-block;
  background-color: black;
  color: white;
}

.restaurant-item .restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {
  margin-left: 10px;
}

.restaurant-item .restaurant-item__content {
  padding: 16px;
}

.restaurant-item .restaurant-item__content h3 {
  margin: 0 0 10px 0;
}

.restaurant-item .restaurant-item__content h3 a {
  color: #362417;
  text-decoration: none;
  padding: 12px;  
  box-sizing: border-box; 
  min-width: 44px; 
  min-height: 44px;
  display: inline-block; 
  font-size: 1.5rem;
}

.restaurant-item .restaurant-item__content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; 
  -webkit-box-orient: vertical;
  padding-left: 12px;
  font-size: 1.2rem;
}

/*
  Restaurant Detail
*/

.detail_content .content__heading {
  padding: 2rem 0  0 2rem;
  text-align: center;
  font-weight:400;
}

.restaurant-detail {

  padding: 1rem 2rem 2rem 4rem;
}

.restaurant__title {
  font-size: 2rem;
  border-bottom: 1px solid  #362417 ;
}

.info_container {
  display: flex;
  gap: 2rem;
}

.restaurant-detail .restaurant__poster {
  width: 100%;
  max-width: 400px;
  padding: 1rem 1rem 1rem 0rem;
}

.restaurant-detail .restaurant__info h3 {
  font-size: 1.5rem;
  padding-top: .5rem;
}

.restaurant-detail .restaurant__info h4 {
  padding-top: .8rem;
  font-size: 1.1rem;
}

.restaurant-detail .restaurant__info p {
  font-size: 1rem;
}

.restaurant__menus h3 {
  text-align: center;
  font-size: 1.5rem;
  padding-top: 4rem;
  border-bottom:1px solid #362417 ;
}

.menus_container {
  display: flex;
  justify-content: space-around;
  padding-top: 2rem;
}

.restaurant__menus__foods  {
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
  padding: 3rem 6rem 3rem 6rem;

}

.restaurant__menus__foods h4 {
  text-align: center;
  font-size: 1.5rem;
  padding: 1rem;

}


.restaurant__menus__drinks {
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
  padding: 3rem 6rem 3rem 6rem;
}

.restaurant__menus__drinks h4 {
  text-align: center;
  font-size: 1.5rem;
  padding: 1rem;
}

.restaurant__reviews h3 {
  text-align: center;
  font-size: 1.5rem;
  padding-top: 4rem;
  border-bottom:1px solid #362417 ;
}

.review {
  display: flex;
  flex-direction: column;
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  padding: 2rem;
}

.review h5 {
  font-weight: 300;
  padding-top: 1rem;
}
.review p {
  text-align: right;
}

.review_container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 2rem;
  padding: 1rem;

}

/* 
Favorite Button 
*/

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}



/*
  Footer
*/

.footer {
  color: #362417;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3rem;
  border-top: 1px solid #ccc;
}

.logo h1 {
  font-family: 'Playfair Display', serif;
}

.logo span {
  font-family: 'Playfair Display', serif;
  color: #362417;
}

.footer-extra {
  display: flex;
  flex-direction: row;
  gap: 6rem;
}

.sosmed {
  margin-top: 2rem;
  align-items: center;
  justify-content: center;
}

.sosmed h1 {
  color: #362417;
  font-size: 1.5rem;
  font-weight: 600;
}

.sosmed a,
.email a {
  font-size: 1.8rem;
  color:#362417;
  text-decoration: none;
  margin-right: 3rem;
  cursor: pointer;
  padding: 14px;  
  box-sizing: border-box; 
  min-width: 44px; 
  min-height: 44px; 
  display: inline-block; 
}



.sosmed a:hover {
  color: rgb(0, 0, 0);
}

.email {
  margin-top: 2rem;
  padding-right: 2rem;
  
}

.email h1 {
  color:#362417;
  font-size: 1.5rem;
  font-weight: 600;
}

.email a {
  color: #362417;
  font-size: 1.2rem;
}

footer {
  padding: 16px;
}

footer p {
  padding: 1rem;
  background-color: #362417;
  text-align: center;
  color: white;
}

